.subtotal {
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 100px;
  padding: 20px;
  background-color: #f3f3f3;
  border: 1px solid #dddddd;
  border-radius: 3px;
  justify-content: space-between;
}

.subtotal__gift {
  display: flex;
  align-items: center;
}

.subtotal__gift input {
  margin-right: 5px;
}
.subtotal button {
    background-color: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;

    color: #111;
}